export default {
	data() {
		var This = this;
		return {
			labelfocus:'',
			msg:This.value?This.after:This.before
		};
	},
	props: {
		type: {
			type: String,
			value: 'text'
		},
		password: {
			type: Boolean,
			value: false
		},
		name:{
			type: String,
			value: ''
		},
		before:{
			type: String,
			value: '请输入'
		},
		after:{
			type: String,
			value: '名称'
		},
		value:{
			type: String,
			value:''
		},
		disabled:{
			type:Boolean,
			value:false
		},
		errmsg:{
			type: String,
			value: ''
		}
	},
	created() {
		var This = this;
		This.labelfocus = This.value ? 'labelfocus' : '';
	},
	methods:{
		focus: function (e) {
			var This = this;
			This.$emit("focus", e);
			var val = e.detail.value;
			This.labelfocus = 'labelfocus';
		},
		blur: function (e) {
			var This = this;
			This.$emit("blur", e);
			var val = e.detail.value;
			if(val!=''){
				This.labelfocus = 'labelfocus';
			}else{
				This.labelfocus = '';
				This.value = '';
			}
		},
		input: function (e) {
			var This = this;
			This.$emit("input", e);
			var val = e.detail.value;
			This.msg = val!='' ? This.after : This.before;
		}
	}
}
